/*
  -------------------------------------------------------
   Imports:
  -------------------------------------------------------
*/
import LoadingIcon from "assets/img/common/Loading.svg";
import eyeIcon from "assets/img/common/eye_icon.svg";
import sortIcon from "assets/img/sortIcon.svg";
import sortIconAsc from "assets/img/Ascending_Sort.svg";
import sortIconsDesc from "assets/img/Descending_Sort.svg";
import AutoComplete from "components/AutoComplete";
import ButtonBox from "components/ButtonBox";
import DatePicker from "components/DatePicker";
import DialogBox from "components/Dialog/Dialog";
import InputBox from "components/InputBox";
import MenuBox from "components/MenuBox/MenuBox";
import PageTitle from "components/PageTitle";
import ReadOnlyTable from "components/ReadOnlyTable";
import SimpleDropDown from "components/SimpleDropDown";
import Text from "components/Text";
import ToastCustom from "components/Toast/Toast";
import { RouteProps, withRouter } from "hoc/withRouter";
import { CalendarChangeParams } from "primereact/calendar";
import { Column } from "primereact/column";
import { ChangeEvent, useEffect, useRef, useState } from "react";
import {
  linkProductConfigurationProductsCreateViewEdit,
  linkStaticMasterTypesCreateViewEdit,
} from "routes";
import ProductConfigProductService from "services/masterdata/ProductConfig/ProductConfigProductService";
import StaticMasterTypeService from "services/masterdata/StaticMaster/StaticMasterTypeService";
import {
  convertDateToISO,
  formattedDateLong,
  parseStandardAPIErrorMessage,
  stringifyObject,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  DROPDOWN_STYLE_TYPE,
  ENTITY,
  INPUT_STYLE_TYPE,
  IS_ACTIVE,
  OPERATION_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import SectionTitle from "components/SectionTitle/SectionTitle";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";

/*
  -------------------------------------------------------
  PROPS:
  -------------------------------------------------------
*/

function PageProductListing(props: RouteProps) {
  /*
    -------------------------------------------------------
      States
    -------------------------------------------------------
  */

  const [showFilter, setShowFilter] = useState(false);
  const [showDialogDeactivate, setShowDialogDeactivate] = useState(false);
  const [showDialogActivate, setShowDialogActivate] = useState(false);
  const [selectedData, setSelectedData] = useState<any>({});
  const [nameAsc, setnameAsc] = useState<any>(undefined);
  const [productTypeAsc, setProductTypeAsc] = useState<any>(undefined);
  const [loanTypeAsc, setLoanTypeAsc] = useState<any>(undefined);
  const [createdAtAsc, setCreatedAtAsc] = useState<any>(undefined);
  const [createdByAsc, setCreatedByAsc] = useState<any>(undefined);
  const [updatedAtAsc, setUpdatedAtAsc] = useState<any>(undefined);
  const [updatedByAsc, setUpdatedByAsc] = useState<any>(undefined);
  const [isActiveAtAsc, setIsActiveAsc] = useState<any>(undefined);
  const [productTypeList, setProductTypeList] = useState<any>([]);
  const [loanTypeList, setLoanTypeList] = useState<any>([]);
  const toastMessageRef = useRef(null);
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [isActivateClicked, setIsActivateClicked] = useState(false);
  const [triggerToast, setTriggerToast] = useState(false);

  const [searchObj, setSearchObj] = useState({
    name: "",
    productType: "",
    loanType: "",
    createdFrom: "",
    createdTo: "",
    updatedFrom: "",
    updatedTo: "",
    isActive: "",
    createdBy: "",
    updatedBy: "",
  });
  const [initialObj, setInitialObj] = useState({
    title: "",
    id: "",
    userId: "",
    completed: "",
    concept: "",
    application: "",
    emailId: "",
  });
  const [dataTable, setDataTable] = useState({
    limit: 10,
    loading: false,
    totalRecords: 0,
    start: 0,
    search: "",
    runCustomQuery: false,
    records: [],
  });
  const activeRowRef = useRef(null);
  const [editModuleCardMenu, setEditModuleCardMenu] = useState<any>([
    {
      items: [
        {
          label: "Activate",
          className: "",
        },
        {
          label: "Deactivate",
          className: "",
        },
      ],
    },
  ]);
  const onToggleMenu = (rowData: any, e: any) => {
    const isActive = {
      label: "Deactivate",
      command: () => {
        deactivateRow(rowData);
      },
    };
    const isNotActive = {
      label: "Activate",
      command: () => {
        activateRow(rowData);
      },
    };

    const item = rowData.isActive ? isActive : isNotActive;
    const list = [
      {
        items: [item],
      },
    ];
    setEditModuleCardMenu(list);

    // @ts-ignore: Will fix later
    activeRowRef?.current?.toggle(e);
  };
  const [createdByTemplate, setCreatedByTemplate] = useState<
    {
      name?: string | undefined;
      designation?: string | undefined;
      emailId?: string | undefined;
    }[]
  >([
    {
      name: "Shivam Soni",
      designation: "Credit, Manager, Karnataka",
      emailId: "Shivamsoni@sahayya.com",
    },
    {
      name: "Shiva Prakash",
      designation: "Executive, Chief Executive Officer, Karnataka",
      emailId: "shiva.prakash@sahayya.com",
    },
    {
      name: "Rohith Balakrishnan",
      designation: "Head, Country Head, Karnataka",
      emailId: "Rohith.balakrishnan@sahayya.com",
    },
  ]);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<
    {
      name?: string | undefined;
      designation?: string | undefined;
      emailId?: string | undefined;
    }[]
  >([
    {
      name: "Shivam Soni",
      designation: "Credit, Manager, Karnataka",
      emailId: "Shivamsoni@sahayya.com",
    },
    {
      name: "Shiva Prakash",
      designation: "Executive, Chief Executive Officer, Karnataka",
      emailId: "shiva.prakash@sahayya.com",
    },
    {
      name: "Rohith Balakrishnan",
      designation: "Head, Country Head, Karnataka",
      emailId: "Rohith.balakrishnan@sahayya.com",
    },
  ]);

  const dataTableRef = useRef(null);

  /*
    -------------------------------------------------------
      Hooks: useEffect
    -------------------------------------------------------
  */

  useEffect(() => {
    // @ts-ignore: will fix later
    dataTableRef?.current?.search();
  }, [dataTable?.search]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will fix later
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    //Get all dropdown options
    getProductType();
    getLoanType();
  }, []);

  const getProductType = () => {
    StaticMasterDataService.getAll(0, 10, `staticMasterType.code=PRODUCT_NAME`)
      .then((res: any) => {
        setProductTypeList([...res.data.results] ?? []);
      })
      .catch((err) => {
        console.log("err");
      });
  };

  const getLoanType = () => {
    StaticMasterDataService.getAll(0, 10, `staticMasterType.code=LOAN_TYPE`)
      .then((res: any) => {
        setLoanTypeList([...res.data.results] ?? []);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  /*
    -------------------------------------------------------
      Handlers: Event handler for onChange, onBlur etc
    -------------------------------------------------------
  */
  const handleFilterChange = (event: ChangeEvent<HTMLInputElement> | CalendarChangeParams) => {
    const { name, value } = event.target;
    setSearchObj((prevFilterObj) => ({
      ...prevFilterObj,
      [name]: value,
    }));
  };

  const sortname = (event: any) => {
    let search = "";
    if (nameAsc || nameAsc == undefined) {
      search = `&sort={"name":"ASC"}`;
    } else {
      search = `&sort={"name":"DESC"}`;
    }

    if (nameAsc == undefined) {
      setnameAsc(false);
    } else {
      setnameAsc(!nameAsc);
    }
    setProductTypeAsc(undefined);
    setLoanTypeAsc(undefined);
    setCreatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortProductType = (event: any) => {
    let search = "";

    if (productTypeAsc) {
      search = `&sort={"productType":{"name" : "ASC"}}`;
    } else {
      search = `&sort={"productType":{"name" : "DESC"}}`;
    }

    if (productTypeAsc == undefined) {
      setProductTypeAsc(false);
    } else {
      setProductTypeAsc(!productTypeAsc);
    }

    setProductTypeAsc(!productTypeAsc);
    setnameAsc(undefined);
    setLoanTypeAsc(undefined);
    setCreatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);
    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortLoanType = (event: any) => {
    let search = "";
    if (loanTypeAsc) {
      search = `&sort={"loanType":{"name" : "ASC"}}`;
    } else {
      search = `&sort={"loanType":{"name" : "DESC"}}`;
    }

    if (loanTypeAsc == undefined) {
      setLoanTypeAsc(false);
    } else {
      setLoanTypeAsc(!loanTypeAsc);
    }
    setnameAsc(undefined);
    setProductTypeAsc(undefined);
    setCreatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);
    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortCreatedOn = (event: any) => {
    let search = "";
    if (createdAtAsc || createdAtAsc == undefined) {
      search = `&sort={"createdAt":"ASC"}`;
    } else {
      search = `&sort={"createdAt":"DESC"}`;
    }

    if (createdAtAsc == undefined) {
      setCreatedAtAsc(false);
    } else {
      setCreatedAtAsc(!createdAtAsc);
    }

    setnameAsc(undefined);
    setProductTypeAsc(undefined);
    setLoanTypeAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortCreatedBy = (event: any) => {
    let search = "";
    if (createdByAsc || createdByAsc == undefined) {
      search = `&sort={"createdBy":"ASC"}`;
    } else {
      search = `&sort={"createdBy":"DESC"}`;
    }

    if (createdByAsc == undefined) {
      setCreatedByAsc(false);
    } else {
      setCreatedAtAsc(!createdAtAsc);
    }

    setnameAsc(undefined);
    setProductTypeAsc(undefined);
    setLoanTypeAsc(undefined);
    setCreatedAtAsc(undefined);
    setIsActiveAsc(undefined);
    setUpdatedAtAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortUpdatedOn = (event: any) => {
    let search = "";
    if (updatedAtAsc || updatedAtAsc == undefined) {
      search = `&sort={"updatedAt":"ASC"}`;
    } else {
      search = `&sort={"updatedAt":"DESC"}`;
    }

    if (updatedAtAsc == undefined) {
      setUpdatedAtAsc(false);
    } else {
      setUpdatedAtAsc(!updatedAtAsc);
    }

    setnameAsc(undefined);
    setProductTypeAsc(undefined);
    setLoanTypeAsc(undefined);
    setCreatedAtAsc(undefined);
    setIsActiveAsc(undefined);
    setCreatedByAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortIsActive = (event: any) => {
    let search = "";
    if (isActiveAtAsc || isActiveAtAsc == undefined) {
      search = `&sort={"isActive":"ASC"}`;
    } else {
      search = `&sort={"isActive":"DESC"}`;
    }

    if (isActiveAtAsc == undefined) {
      setIsActiveAsc(false);
    } else {
      setIsActiveAsc(true);
    }

    setnameAsc(undefined);
    setProductTypeAsc(undefined);
    setLoanTypeAsc(undefined);
    setCreatedAtAsc(undefined);
    setUpdatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setUpdatedByAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const sortUpdatedBy = (event: any) => {
    let search = "";
    if (updatedByAsc || updatedByAsc == undefined) {
      search = `&sort={"updatedBy":"ASC"}`;
    } else {
      search = `&sort={"updatedBy":"DESC"}`;
    }

    if (updatedByAsc == undefined) {
      setUpdatedByAsc(false);
    } else {
      setUpdatedByAsc(!updatedByAsc);
    }

    setnameAsc(undefined);
    setProductTypeAsc(undefined);
    setLoanTypeAsc(undefined);
    setCreatedAtAsc(undefined);
    setUpdatedAtAsc(undefined);
    setCreatedByAsc(undefined);
    setIsActiveAsc(undefined);

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const activateRow = (rowData: any) => {
    setSelectedData({ ...rowData });
    setShowDialogActivate(true);
    setIsActivateClicked(true);
  };

  const deactivateRow = (rowData: any) => {
    setSelectedData({ ...rowData });
    setShowDialogDeactivate(true);
    setIsActivateClicked(false);
  };

  const onSaveDeactivate = () => {
    return ProductConfigProductService.Inactivate(selectedData?.uid)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage("Deactivated Successfully");
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore: will fix later
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  const onSaveActivate = () => {
    const enrichedObj = {
      ...selectedData,
      isActive: true,
    };
    return ProductConfigProductService.activate(selectedData?.uid)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage("Activated Successfully");
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore: will fix later
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  const onApproveActivate = () => {
    const initialJsonObject = {
      ...selectedData,
    };
    const finalJsonObject = {
      ...initialJsonObject,
      isActive: true,
    };
    const enrichedObj = {
      name: selectedData.name,
      operationType: OPERATION_TYPE.ACTIVE,
      entity: ENTITY.PRODUCT,
      initialStateJson: stringifyObject(initialJsonObject),
      finalStateJson: stringifyObject(finalJsonObject),
      reqPayload: stringifyObject(finalJsonObject),
      reasonForChange: "Activate",
    };
    return StaticMasterTypeService.approve(enrichedObj)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SENT_FOR_APPROVAL);
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore: Will fix later
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  const onApproveDeActivate = () => {
    console.log(selectedData);
    const initialJsonObject = {
      ...selectedData,
    };
    const finalJsonObject = {
      ...initialJsonObject,
      isActive: false,
    };
    const enrichedObj = {
      name: selectedData.name,
      operationType: OPERATION_TYPE.INACTIVE,
      entity: ENTITY.PRODUCT,
      initialStateJson: stringifyObject(initialJsonObject),
      reqPayload: stringifyObject(finalJsonObject),
      finalStateJson: stringifyObject(finalJsonObject),
      reasonForChange: "Deactivate",
    };
    return StaticMasterTypeService.approve(enrichedObj)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SENT_FOR_APPROVAL);
        setTriggerToast(!triggerToast);
        setShowDialogDeactivate(false);
        setShowDialogActivate(false);
        // @ts-ignore: Will fix later
        dataTableRef?.current?.search();
      })
      .catch((err) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
        setToastMessage(parseStandardAPIErrorMessage(err));
        setTriggerToast(!triggerToast);
      });
  };

  /* 
    -------------------------------------------------------
      Helper Functions:
    -------------------------------------------------------
  */

  const search = () => {
    let search = "";
    if (searchObj.name) {
      search = `&${search}name:lk=${searchObj.name}`;
    }
    if (searchObj.productType) {
      search = `&${search}productType.name:lk=${searchObj.productType}`;
    }
    if (searchObj.loanType) {
      search = `&${search}loanType.name:lk=${searchObj.loanType}`;
    }
    if (searchObj.createdFrom) {
      console.log(searchObj.createdFrom);
      search = `&${search}createdAt:gt=${convertDateToISO(searchObj.createdFrom)}`;
    }
    if (searchObj.createdTo) {
      search = `&${search}createdAt:lt=${convertDateToISO(searchObj.createdTo)}`;
    }
    if (searchObj.updatedFrom) {
      search = `&${search}updatedAt:gt=${convertDateToISO(searchObj.updatedFrom)}`;
    }
    if (searchObj.updatedTo) {
      search = `&${search}updatedAt:lt=${convertDateToISO(searchObj.updatedTo)}`;
    }
    if (searchObj.isActive) {
      let isActiveBool = true;
      if (searchObj.isActive === "YES") {
        isActiveBool = true;
      } else if (searchObj.isActive === "NO") {
        isActiveBool = false;
      }
      search = `&${search}isActive:lk=${isActiveBool}`;
    }
    if (searchObj.createdBy) {
      search = `&${search}&createdBy=${searchObj.createdBy}`;
    }
    if (searchObj.updatedBy) {
      search = `&${search}&updatedBy=${searchObj.updatedBy}`;
    }
    setDataTable({
      ...dataTable,
      search,
    });
  };

  const searchAutoCompleteCreatedBy = (event: any) => {
    let suggestionsTemplate = [];

    if (!event.query.trim().length) {
      suggestionsTemplate = createdByTemplate;
    } else {
      suggestionsTemplate = createdByTemplate.filter((list) => {
        if (list.name) {
          return list.name.toLowerCase().startsWith(event.query.toLowerCase());
        }
      });
      if (suggestionsTemplate.length === 0) {
        suggestionsTemplate.push({ name: undefined, designation: undefined, emailId: undefined });
      }
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  /* 
    -------------------------------------------------------
      ButtonClick Functions:
    -------------------------------------------------------
  */
  const onToggleFilter = () => {
    setShowFilter(!showFilter);
  };
  const onCreate = () => {
    props.navigate(`${linkProductConfigurationProductsCreateViewEdit}?mode=${PAGE_MODE.CREATE}`);
  };
  const onClearFilter = () => {
    setSearchObj({
      name: "",
      productType: "",
      loanType: "",
      createdFrom: "",
      createdTo: "",
      updatedFrom: "",
      updatedTo: "",
      isActive: "",
      createdBy: "",
      updatedBy: "",
    });
    setDataTable({
      ...dataTable,
      search: "",
    });
  };

  /* 
    -------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    -------------------------------------------------------
  */

  const renderFilter = () => {
    const {
      name,
      productType,
      loanType,
      createdFrom,
      createdTo,
      updatedFrom,
      updatedTo,
      isActive,
      createdBy,
      updatedBy,
    } = searchObj;
    return (
      <>
        <div className=" p-3 flex flex-col gap-6 bg-primary-sapphireBlue-100 rounded-lg">
          <div className="flex">
            <SectionTitle title="text.masterData.types.search" />
          </div>
          <div className=" grid grid-cols-1 md:grid-cols-4 gap-3">
            <div className="">
              <InputBox
                label="label.productConfiguration.products.productShortNameFilter"
                name="name"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                onChange={handleFilterChange}
                value={name}
              />
            </div>

            <div className="">
              <SimpleDropDown
                label="input.masterData.productConfiguration.productTypeFilter"
                name="productType"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                onChange={handleFilterChange}
                value={productType}
                options={productTypeList}
                optionLabel="name"
                optionValue="name"
              />
            </div>

            <div className="">
              <SimpleDropDown
                label="input.masterData.productConfiguration.loanTypeFilter"
                name="loanType"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                onChange={handleFilterChange}
                value={loanType}
                options={loanTypeList}
                optionLabel="name"
                optionValue="name"
              />
            </div>

            <div className="">
              <SimpleDropDown
                label="input.masterData.staticMaster.isActive"
                name="isActive"
                variant={DROPDOWN_STYLE_TYPE.SECONDARY}
                options={IS_ACTIVE}
                onChange={handleFilterChange}
                value={isActive}
              />
            </div>
            <div className="">
              <DatePicker
                label="input.masterData.staticMaster.createdFrom"
                name="createdFrom"
                placeholder="DD-MMM-YYYY"
                showTime
                value={createdFrom}
                onChange={handleFilterChange}
              />
            </div>
            <div className="">
              <DatePicker
                showTime
                label="input.masterData.staticMaster.createdTo"
                name="createdTo"
                placeholder="DD-MMM-YYYY"
                variant={INPUT_STYLE_TYPE.PRIMARY}
                value={createdTo}
                onChange={handleFilterChange}
              />
            </div>
            <div className="col-span-1">
              <AutoComplete
                label="input.masterData.staticMaster.createdBy"
                name="createdBy"
                field="name"
                value={createdBy}
                variant={INPUT_STYLE_TYPE.PRIMARY}
                suggestions={suggestionsTemplate}
                completeMethod={searchAutoCompleteCreatedBy}
                onChange={handleFilterChange}
                itemTemplate={itemTemplate}
              />
            </div>
            <div className="">
              <DatePicker
                showTime
                label="input.masterData.staticMaster.updatedFrom"
                name="updatedFrom"
                placeholder="DD-MMM-YYYY"
                variant={INPUT_STYLE_TYPE.PRIMARY}
                value={updatedFrom}
                onChange={handleFilterChange}
              />
            </div>
            <div className="">
              <DatePicker
                showTime
                label="input.masterData.staticMaster.updatedTo"
                name="updatedTo"
                placeholder="DD-MMM-YYYY"
                variant={INPUT_STYLE_TYPE.PRIMARY}
                value={updatedTo}
                onChange={handleFilterChange}
              />
            </div>
            <div className="col-span-1">
              <AutoComplete
                field="name"
                label="input.masterData.staticMaster.updatedBy"
                name="updatedBy"
                variant={INPUT_STYLE_TYPE.PRIMARY}
                value={updatedBy}
                suggestions={suggestionsTemplate}
                completeMethod={searchAutoCompleteCreatedBy}
                onChange={handleFilterChange}
                itemTemplate={itemTemplate}
              />
            </div>
            <div>
              <div className="flex  justify-start mb-2">
                <div className="mr-5">
                  <ButtonBox buttonType={BUTTON_TYPE.RESET_OUTLINE} onClick={onClearFilter} />
                </div>
                <div>
                  <ButtonBox buttonType={BUTTON_TYPE.SEARCH_OUTLINE} onClick={search} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  };

  const itemTemplate = (item: any) => {
    if (item.name === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <img src={LoadingIcon} alt="search-icon" className=" h-7 w-7" />
          <Text className="text-primary-labelColor mt-2" label="Loading..." />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={item.name} />
        <Text className="text-secondary-gray-500 text-xxs" label={item.designation} />
        <Text className="text-secondary-gray-500 text-xxs" label={item.emailId} />
      </div>
    );
  };

  const rendername = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left truncate">
          {rowData.name}
        </div>
      </>
    );
  };

  const renderHeadername = () => {
    return (
      <div className="flex">
        <p className="mr-3">Product Short Name</p>
        <img
          src={nameAsc == undefined ? sortIcon : nameAsc == true ? sortIconAsc : sortIconsDesc}
          alt="Sort Icon"
          onClick={sortname}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderProductType = () => {
    return (
      <div className="flex">
        <p className="mr-1">Product Type</p>
        <img
          src={
            productTypeAsc == undefined
              ? sortIcon
              : productTypeAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortProductType}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderLoanType = () => {
    return (
      <div className="flex">
        <p className="mr-1">Loan Type</p>
        <img
          src={
            loanTypeAsc == undefined ? sortIcon : loanTypeAsc == true ? sortIconAsc : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortLoanType}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderCreatedOn = () => {
    return (
      <div className="flex">
        <p className="mr-1">Created On</p>
        <img
          src={
            createdAtAsc == undefined
              ? sortIcon
              : createdAtAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortCreatedOn}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderCreatedBy = () => {
    return (
      <div className="flex">
        <p className="mr-1">Created By</p>
        <img
          src={
            createdByAsc == undefined
              ? sortIcon
              : createdByAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortCreatedBy}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderUpdatedOn = () => {
    return (
      <div className="flex">
        <p className="mr-1">Updated On</p>
        <img
          src={
            updatedAtAsc == undefined
              ? sortIcon
              : updatedAtAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortUpdatedOn}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderUpdatedBy = () => {
    return (
      <div className="flex">
        <p className="mr-1">Updated By</p>
        <img
          src={
            updatedByAsc == undefined
              ? sortIcon
              : updatedByAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortUpdatedBy}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderHeaderIsActive = () => {
    return (
      <div className="flex">
        <p className="mr-1">Is Active</p>
        <img
          src={
            isActiveAtAsc == undefined
              ? sortIcon
              : isActiveAtAsc == true
              ? sortIconAsc
              : sortIconsDesc
          }
          alt="Sort Icon"
          onClick={sortIsActive}
          className="cursor-pointer"
        />
      </div>
    );
  };

  const renderProductType = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left truncate">
          {rowData.productType?.name}
        </div>
      </>
    );
  };

  const renderLoanType = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left truncate">
          {rowData.loanType.name}
        </div>
      </>
    );
  };

  const renderCreatedOn = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left truncate">
          {`${formattedDateLong(rowData.createdAt)}`}
        </div>
      </>
    );
  };

  const renderCreatedBy = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left truncate">
          {rowData.createdByUserName}
        </div>
      </>
    );
  };

  const renderUpdatedOn = (rowData: any) => {
    return (
      <div className="text-primary-ptext-900 font-BrownLight text-left truncate">
        {`${formattedDateLong(rowData.updatedAt)}`}
      </div>
    );
  };

  const renderUpdatedBy = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-900 font-BrownLight text-left truncate">
          {rowData.updatedByUserName}
        </div>
      </>
    );
  };

  const renderIsActive = (rowData: any) => {
    return (
      <>
        {rowData.isActive ? (
          <div className="flex items-center ">
            <div className="w-2 h-2 rounded-full bg-primary-primaryBlue border-none"></div>
            <div className="ml-2 ">YES</div>
          </div>
        ) : (
          <div className="flex items-center ">
            <div className="w-2 h-2 rounded-full bg-secondary-gray-300 border-none"></div>
            <div className="ml-2 ">NO</div>
          </div>
        )}
      </>
    );
  };

  const renderActions = (rowData: any) => {
    return (
      <>
        <div className="flex items-center ">
          <div className="w-4 h-4 flex items-center">
            <img
              src={eyeIcon}
              alt="Sahayya Logo"
              className="cursor-pointer"
              onClick={(e: any) => {
                props.navigate(
                  `${linkProductConfigurationProductsCreateViewEdit}?mode=${PAGE_MODE.VIEW}&uid=${rowData.uid}`
                );
              }}
            />
          </div>
          <div className="ml ">
            <ButtonBox
              buttonType={BUTTON_TYPE.MENU}
              onClick={(e) => {
                onToggleMenu(rowData, e);
              }}
              className="!rounded-none bg-transparent !hover:bg-primary-gray-300"
            />
          </div>
        </div>
      </>
    );
  };

  return (
    <>
      <div className="flex flex-col gap-3">
        <div className="flex w-full justify-between items-center">
          <div className=" h-7.5">
            <Text
              label="heading.productConfiguration.products"
              className="text-primary-ptext-1000 text-h2 font-Roboto font-bold flex"
            />
          </div>
          <div className="flex mr-5">
            <div className="mr-5">
              <ButtonBox buttonType={BUTTON_TYPE.FILTER_OUTLINE} onClick={onToggleFilter} />
            </div>
            <div>
              <ButtonBox
                buttonType={BUTTON_TYPE.CREATE}
                className="create-button"
                onClick={onCreate}
              />
            </div>
          </div>
        </div>
        {showFilter && renderFilter()}

        <ReadOnlyTable
          standardDataFormatPromise={ProductConfigProductService.getAll}
          dataKey="id"
          search={dataTable.search}
          ref={dataTableRef}
        >
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderProductType}
            body={renderProductType}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs w-1/12  p-1 h-12 pl-6"
            header={renderHeaderLoanType}
            body={renderLoanType}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeadername}
            body={rendername}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderCreatedOn}
            body={renderCreatedOn}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderCreatedBy}
            body={renderCreatedBy}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderUpdatedOn}
            body={renderUpdatedOn}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={renderHeaderUpdatedBy}
            body={renderUpdatedBy}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
            header={renderHeaderIsActive}
            body={renderIsActive}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
            header="Actions"
            body={renderActions}
          />
        </ReadOnlyTable>
      </div>
      <DialogBox
        onHide={
          isActivateClicked
            ? () => setShowDialogActivate(false)
            : () => setShowDialogDeactivate(false)
        }
        visible={isActivateClicked ? showDialogActivate : showDialogDeactivate}
        modal
        closable={false}
        dismissableMask={true}
      >
        <Text
          label={isActivateClicked ? "dialog.product.activate" : "dialog.product.deactivate"}
          className="text-primary-ptext-1000 text-base font-Roboto font-bold flex items-center justify-center"
        />
        <Text
          label={
            isActivateClicked
              ? "dialog.staticMaster.message.activate"
              : "dialog.staticMaster.message"
          }
          className="text-primary-ptext-1000 text-xs font-Roboto flex mt text-center justify-center mx-2 mt-4"
        />
        <div className="flex justify-center mt-4">
          <div className="mr-3">
            <ButtonBox
              buttonType={BUTTON_TYPE.CANCEL_OUTLINE}
              onClick={
                isActivateClicked
                  ? () => setShowDialogActivate(false)
                  : () => setShowDialogDeactivate(false)
              }
            />
          </div>
          <div className="mr-3">
            <ButtonBox
              buttonType={BUTTON_TYPE.SAVE_OUTLINE}
              onClickWithLoader={
                isActivateClicked ? () => onSaveActivate() : () => onSaveDeactivate()
              }
            />
          </div>
          <div className="">
            <ButtonBox
              buttonType={BUTTON_TYPE.SEND_FOR_APPROVAL}
              className="approve-primary"
              onClickWithLoader={
                isActivateClicked ? () => onApproveActivate() : () => onApproveDeActivate()
              }
            />
          </div>
        </div>
      </DialogBox>
      <MenuBox
        className="w-20 bg-white font-Roboto text-xs text-primary-pText-1300 pt-2 rounded-lg mt-0.5 flex justify-center align-center shadow-autoCompletePanel"
        model={editModuleCardMenu}
        ref={activeRowRef}
      />
      <div className="">
        <ToastCustom
          position="bottom-right"
          ref={toastMessageRef}
          toastText={toastMessage}
          variant={toastMessageVariant}
        />
      </div>
    </>
  );
}

export default withRouter(PageProductListing);
